.middle-side-panel-box-wrap {
  flex: 1;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  max-width: 400px;
  min-width: 400px;
  padding-bottom: 12px;
  padding: 10px 0;

  .middle-side-panel-box {
    display: flex;
    flex-direction: column;
    height: 100%;
    border-left: 1px dashed #ccc;

    .basic-info-box {
      flex: 2;
      display: flex;
      flex-direction: column;
      padding: 0 15px;

      .title {
        height: 35px;
        line-height: 35px;
        margin-bottom: 5px;
        font-size: 14px;
      }
    }

    .nodule-list-box {
      flex: 5;
      height: 320px;
      max-height: 800px;
      display: flex;
      flex-direction: column;
      padding: 0 15px;

      .title {
        height: 35px;
        line-height: 35px;
        font-size: 14px;
      }

      .table-box {
        display: flex;
        flex-direction: column;
        border: 1px solid #0e2b40;
        overflow: hidden;

        .table-title {
          display: flex;
          height: 25px;
          line-height: 25px;
          text-align: center;
          border: 1px solid transparent;
          padding: 0 10px 0 5px;

          .lung,
          .lobe {
            flex: 2;
          }

          .num,
          .type,
          .suggest {
            flex: 3;
          }

          .suggest {
            max-width: 85px;
          }

          .size {
            flex: 3;
          }
        }

        .table-content {
          // display: flex;
          flex-direction: column;
          overflow-x: hidden;
          overflow-y: overlay;
          min-height: 500px;

          .table-item {
            user-select: none;
            display: flex;
            height: 25px;
            line-height: 25px;
            text-align: center;
            border: 1px solid transparent;
            transition: all 0.1s;
            padding: 0 10px 0 5px;

            &.item-active {
              border: 1px solid #ccc;
              background: #eaeaea;
            }

            &:hover {
              border: 1px solid #ccc;
              background: #eaeaea;
            }
          }

          .lung,
          .lobe {
            flex: 2;
          }

          .num,
          .type,
          .suggest {
            flex: 3;
          }

          .suggest {
            max-width: 85px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }

          .review-state {
            color: #ff4d4f;

            .review {
              color: #73d13d;
            }
          }
        }
      }
    }

    .image-info-box {
      flex: 3;
      display: flex;
      flex-direction: column;
      padding: 0 15px;

      .title {
        height: 35px;
        line-height: 35px;
        font-size: 14px;
      }

      .info-box {
        flex: 1;
        display: flex;
        flex-direction: column;

        .report-box {
          flex: 1;
          display: flex;
          flex-direction: column;
          width: 100%;
          border: 1px solid #0e2b40;

          .title {
            display: flex;
            height: 20px;
            line-height: 20px;
            padding: 0 5px;
          }

          .report-content {
            flex: 1 1 1px;
            overflow-y: overlay;
            word-break: break-all;
            padding: 5px 18px 0 5px;

            .viewer-item {
              margin-bottom: 8px;
              padding: 5px;
              border: 1px solid transparent;
              transition: all 0.1s;
              cursor: default;
              line-height: 18px;

              span {
                color: #1890ff;
              }

              &.item-active {
                border: 1px solid #ccc;
                background: #eaeaea;
              }

              &:hover {
                border: 1px solid #ccc;
                background: #eaeaea;
              }

              &:last-child {
                margin-bottom: 0;
              }
            }
          }
        }

        .suggest-box {
          flex: 1;
          display: flex;
          flex-direction: column;
          width: 100%;

          .title {
            height: 35px;
            line-height: 35px;
          }

          .suggest-content {
            padding-bottom: 40px;
            flex: 1 1 1px;
            overflow: hidden;

            .suggest-content-wrap {
              border: 1px solid #0e2b40;
              width: 100%;
              height: 100%;
              padding: 10px 18px 10px 10px;
              word-break: break-all;
              overflow-y: overlay;
              min-height: 120px;

              .suggest-item {
                margin-bottom: 10px;

                &:last-child {
                  margin-bottom: 0;
                }
              }
            }

            .save {
              margin-top: 5px;
              text-align: right;

              .ant-btn {
                font-size: 12px !important;
              }
            }
          }
        }
      }
    }
  }
}

.ant-checkbox-wrapper {
  justify-content: space-between;
  flex: 3 !important;
}

.ant-popover-inner-content {
  padding: 0 !important;
}

.ant-descriptions-item-content {
  font-size: 13px !important;
}
