.viewer-box {
  width: 100%;
  height: 100%;
  .toolBox {
    display: flex;
    .select-file-box {
      height: initial;
    }
  }
  .viewer-center-box {
    position: relative;
    display: flex;
    height: calc(100% - 50px);
    flex-flow: row nowrap;
    align-items: stretch;
  }
}
